.catalogs {
  padding-bottom: 6.25em;
  &__info {
    background: #F5F3F3;
    padding: 3.75em 0;
    margin-top: 3.125em;
  }
  &__bottom {
    margin-top: 1.875em;
  }
  &__btn {
    display: inline-flex !important;
    height: 2.625em;
    padding: 0 1.5em;
    span {
      font-weight: 700;
      text-transform: unset !important;
    }
  }
  &-cards {
    display: grid;
    grid-template-columns: repeat(4, calc(100% / 4 - 5.625em / 4));
    gap: 1.875em;
    margin-top: 1.875em;
  }
}
.catalog-card2 {
  border: 0.0625em solid #C2C6C6;
  border-radius: 0.625em;
  padding: 1.25em;
  text-align: center;
  &__img {
    position: relative;
    padding-bottom: 140%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__desc {
    margin-top: 0.625em;
  }
  &__links {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625em;
    margin-top: 0.625em;
  }
  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    i {
      flex-shrink: 0;
      width: 1.875em;
      margin-right: 0.375em;
      img {
        width: 100%;
      }
    }
    span {
      font-family: GothamPro;
      font-size: 0.9375em;
      line-height: 120%;
      font-weight: 700;
      color: #101112;
    }
  }
}

@media screen and (max-width: 1300px) {
  .catalogs {
    padding-bottom: 3.125em;
    &__info {
      margin-top: 1.875em;
    }
    &-cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 3.75em / 3));
    }
  }
}

@media screen and (max-width: 768px) {
  .catalogs {
    &-cards {
      grid-template-columns: repeat(2, calc(100% / 2 - 1.25em / 2));
      gap: 1.25em;
    }
  }
}

@media screen and (max-width: 400px) {
  .catalogs {
    &-cards {
      grid-template-columns: 100%;
    }
  }
}