.sale {
  padding-bottom: 1.5625em;
  &__top {
    border-bottom: 0.0625em solid #C2C6C6;
    margin-top: 1.875em;
    &-btn {
      display: inline-flex !important;
      height: 3.125em;
      padding: 0 1em;
      span {
        font-size: 1em;
        line-height: 120%;
        font-weight: 700;
        text-transform: unset !important;
      }
    }
  }
  &__filter {
    margin-top: 3.125em;
  }
  &__select {
    display: flex;
    align-items: center;
    border-radius: 0.3125em;
    border: 0.0625em solid #C2C6C6;
    padding: 1.875em 0.75em;
    margin-top: 3.125em;
    &-text {
      margin-right: 1.5625em;
      span {
        color: #101112;
      }
    }
    .nice-select {
      min-width: 12.5em;
      padding: 0 0.9375em 0 0;
      border: none;
      height: auto;
      background-image: url("../img/select-arrow.svg");
      background-position: center;
      background-size: 0.5em auto;
      font-size: unset;
      line-height: unset;
      &:after {
        top: 50%;
        right: 0;
        transform: translate(0, -50%) rotate(0);
        width: 0.5em;
        height: 0.25em;
        background-image: url("../img/select-arrow2.svg");
        background-position: center;
        background-size: 0.5em 0.25em;
      }
      .current {
        font-family: Verdana;
        font-size: 1em;
        line-height: 120%;
        font-weight: 700;
        color: #101112;
      }
      .list {
        width: 100%;
        li {
          font-size: 1em;
        }
      }
    }
  }
}
.sale-materials {
  padding: 1.5625em 0 16.875em 0;
  &__slider {
    margin-top: 0;
  }
  &__slider-nav {
    margin-top: 0;
  }
}

@media screen and (max-width: 1300px) {
  .sale {
    &__filter {
      margin-top: 2em;
    }
    &__select {
      margin-top: 2em;
    }
  }
  .sale-materials {
    padding-bottom: 6.25em;
  }
}

@media screen and (max-width: 576px) {
  .sale-materials {
    &__slider {
      margin-top: 2em;
    }
  }
}

@media screen and (max-width: 400px) {
  .sale {
    &__select {
      flex-direction: column;
      align-items: start;
      &-text {
        margin: 0 0 1em;
      }
    }
  }
}