.main-forms {
  background: #F5F3F3;
  padding: 2.8125em 0;
  &__text {
    margin-top: 2.5em;
  }
  &__btn {
    display: inline-flex !important;
    height: 2.625em;
    padding: 0 1em;
    margin-top: 2.5em;
    span {
      font-size: 1.125em;
      line-height: 120%;
    }
  }
}
.filter-forms {
  padding: 3.125em 0;
  &__top {
    &-btns {
      display: flex;
      align-items: center;
      margin-bottom: 1.875em;
    }
    &-btn {
      height: 3.125em;
      padding: 0 1em;
      span {
        font-size: 1em;
        line-height: 120%;
      }
    }
    &-btn2 {
      height: 3.125em;
      border: 0.0625em solid #E5E5E5;
      border-radius: 0.1875em;
      padding: 0 1em;
      span {
        font-size: 1em;
        line-height: 120%;
        font-weight: 700;
        color: #101112;
      }
      &:hover {
        background: #000000;
        border: 0.0625em solid #000000;
        span {
          color: #ffffff;
        }
      }
    }
  }
  &__bottom {
    margin-top: 1.5625em;
    &-btns {
      display: flex;
      align-items: center;
      margin-top: 0.9375em;
    }
    &-btn {
      width: 17.1875em;
      height: 3.125em;
      span {
        font-size: 1.125em;
        line-height: 120%;
      }
    }
    &-btn2 {
      width: 17.1875em;
      height: 3.125em;
      margin-left: 3.125em;
      span {
        font-size: 1.125em;
        line-height: 120%;
      }
    }
  }
  &__items {
    display: flex;
    align-items: center;
  }
  &__item {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    &:not(:first-child) {
      margin-left: 3.125em;
    }
    &-title {
      color: #8B8D8D;
      margin-right: 1.25em;
      margin-bottom: 1em;
    }
    &-materials {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
    }
    &-material {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 2.3125em;
      border-radius: 0.3125em;
      border: 0.0625em solid #D7D7D7;
      background: #FFF;
      padding: 0 0.3125em;
      margin-bottom: 1em;
      &:not(:last-child) {
        margin-right: 1.25em;
      }
      &:hover {
        background: #000000;
        border: 0.0625em solid #000000;
        span {
          color: #ffffff;
        }
      }
    }
  }
  &__parameters {
    display: inline-flex;
    align-items: center;
    cursor: pointer;
    &-icon {
      flex-shrink: 0;
      width: 2.1875em;
      margin-right: 1.125em;
      img {
        width: 100%;
      }
    }
    &-title {
      font-size: 1.5em;
      line-height: 120%;
      font-weight: 500;
      color: #000000;
      text-transform: uppercase;
    }
  }
  &__subtitle {
    margin: 1.6875em 0 1.25em;
  }
}
.materials3 {
  padding: 3.125em 0;
  .materials__slider2 {
    margin-top: 0;
  }
  .materials__slider-nav2 {
    margin: 0;
  }
  .pagination {
    margin-top: 6.25em;
  }
}

@media screen and (max-width: 1300px) {
  .main-forms {
    padding: 2em 0;
    &__text {
      margin-top: 1.5em;
    }
    &__btn {
      margin-top: 1.5em;
    }
  }
  .filter-forms {
    padding: 3.125em 0 1.5625em;
    &__items {
      display: block;
    }
    &__item {
      display: block;
      &:not(:first-child) {
        margin: 1em 0 0 0;
      }
      &-materials {
        margin: 1em 0 0 0;
      }
    }
  }
  .materials3 {
    padding: 1.5625em 0 3.125em;
    .pagination {
      margin-top: 3.125em;
    }
  }
}

@media screen and (max-width: 576px) {
  .main-forms {
    &__btn {
      span {
        font-size: 1em;
      }
    }
  }
  .filter-forms {
    &__top {
      &-btns {
        display: block;
      }
      &-btn2 {
        margin: 1em 0 0 0;
      }
    }
    &__bottom {
      &-btns {
        display: block;
      }
      &-btn {
        width: 100%;
      }
      &-btn2 {
        width: 100%;
        margin: 1em 0 0 0;
      }
    }
  }
  .materials3 {
    .materials__slider2 {
      margin-top: 2em;
    }
  }
}
