.delivery {
  background: #F5F3F3;
  padding: 3.125em 0;
  margin-top: 3.125em;
  &__info {
    &-title {
      p {
        color: #101112;
      }
    }
    &-text {
      margin-top: 1em;
      p {
        color: #101112;
      }
    }
    &-btn {
      display: inline-flex !important;
      height: 2.875em;
      padding: 0 1.375em;
      margin-top: 1em;
      span {
        font-size: 0.875em;
        line-height: 120%;
      }
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 3.125em;
    margin-top: 3.125em;
  }
  &__item {
    display: flex;
    background: #ffffff;
    border-radius: 0.3125em;
    padding: 1.875em;
    &-icon {
      flex-shrink: 0;
      width: 2.25em;
      img {
        width: 100%;
      }
    }
    &-desc {
      margin-left: 1.25em;
    }
    &-title {
      margin-bottom: 1em;
    }
    &-nav {
      &:not(:last-child) {
        margin-bottom: 1.5625em;
      }
    }
    &-subtitle {
      p {
        color: #101112;
      }
    }
    &-list {
      margin-top: 0.75em;
      li {
        display: flex;
        align-items: center;
        font-size: 1em;
        line-height: 120%;
        color: #101112;
        padding-left: 0.875em;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: 0;
          transform: translate(0, -50%);
          width: 0.5em;
          height: 0.5em;
          border-radius: 50%;
          border: 0.0625em solid #000000;
        }
        &:not(:first-child) {
          margin-top: 0.375em;
        }
        i {
          flex-shrink: 0;
          width: 10.625em;
          margin-left: 0.3125em;
          img {
            width: 100%;
          }
        }
      }

    }
    &-link {
      margin-top: 0.3125em;
      span {
        font-size: 1em;
        line-height: 120%;
        color: #9D0B1D;
      }
    }
    &-flex {
      display: flex;
      justify-content: space-between;
      margin-top: 1.5625em;
    }
    &-info {
      display: flex;
      max-width: 21.5625em;
      &:not(:first-child) {
        margin-left: 10em;
      }
    }
    &-text {
      p {
        color: #101112;
        &:not(:first-child) {
          margin-top: 1.25em;
        }
      }
    }
  }
  &__info-icon {
    flex-shrink: 0;
    width: 1.5em;
    margin-right: 1.375em;
    img {
      width: 100%;
    }
  }
  &__info-desc {
    p {
      color: #101112;
    }
    a {
      line-height: 120%;
      display: block;
      &:hover {
        color: #9D0B1D;
      }
    }
    p:nth-child(2) {
      margin-top: 0.375em;
    }
  }
}

@media screen and (max-width: 1300px) {
  .delivery {
    margin-top: 2em;
    &__item {
      padding: 1.875em 1.25em;
      &-flex {
        display: block;
      }
      &-info {
        &:not(:first-child) {
          margin: 1.875em 0 0 0;
        }
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .delivery {
    &__item {
      display: block;
      &-desc {
        margin: 1.25em 0 0 0;
      }
    }
  }
}