.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn-red {
  background: #9D0B1D;
  border-radius: 0.3125em;
  text-align: center;
  span {
    color: #ffffff;
    text-transform: uppercase;
  }
  &:hover {
    background: #8b0717;
  }
}
.btn-border {
  border: 0.0625em solid #9D0B1D;
  border-radius: 0.3125em;
  text-align: center;
  span {
    color: #9D0B1D;
    text-transform: uppercase;
  }
  i {
    flex-shrink: 0;
    margin-left: 1em;
  }
  &:hover {
    background: #9D0B1D;
    span {
      color: #ffffff;
    }
    i svg path {
      fill: #ffffff;
    }
  }
}
.text18 {
  font-size: 1.125em;
  line-height: 120%;
  text-transform: uppercase;
}
.text16 {
  font-size: 1em;
  line-height: 140%;
}
.text14 {
  font-size: 0.875em;
  line-height: 120%;
}
.text-bold {
  font-weight: 700;
}
.swiper-button-prev {
  width: auto;
  margin-top: 0;
  &:after {
    display: none;
  }
  svg {
    transform: rotate(-180deg);
    width: 2.8125em;
    height: auto;
  }
  svg path {
    stroke: #9D0B1D;
  }
}
.swiper-button-next {
  width: auto;
  margin-top: 0;
  &:after {
    display: none;
  }
  svg {
    width: 2.8125em;
    height: auto;
  }
  svg path {
    stroke: #9D0B1D;
  }
}
.display2 {
  font-size: 3.75em;
  line-height: 120%;
  font-weight: 700;
  color: #000000;
  text-transform: uppercase;
}
.display3 {
  font-size: 2.75em;
  line-height: 120%;
  font-weight: 700;
  color: #000000;
  text-transform: uppercase;
}
.nav {
  padding: 3.125em 0;
  &__list {
    li {
      font-size: 1.5em;
      line-height: 120%;
      color: #000000;
      &:not(:first-child) {
        margin-top: 1em;
      }
      &:hover {
        color: #a61f1f;
      }
    }
  }
}
.breadcrumbs {
  padding: 15.3125em 0 2.1875em 0;
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__item {
    display: flex;
    align-items: center;
    margin-bottom: 0.9375em;
    a {
      font-size: 0.875em;
      line-height: 105%;
      color: #101112;
      opacity: 0.5;
      &:hover {
        opacity: 1;
      }
    }
    &:not(:last-child) {
      margin-right: 0.75em;
    }
    i {
      flex-shrink: 0;
      width: 0.3125em;
      margin-left: 0.75em;
      img {
        width: 100%;
      }
    }
    &--active {
      font-size: 0.875em;
      line-height: 105%;
    }
  }
}
.pagination {
  &__items {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 3.1875em;
    height: 2.75em;
    border: 0.0625em solid #8B8D8D;
    border-radius: 0.3125em;
    font-size: 1em;
    line-height: 120%;
    font-weight: 700;
    color: #8B8D8D;
    &--active {
      background: #9D0B1D;
      color: #ffffff;
    }
    &:not(:first-child) {
      margin-left: 0.75em;
    }
    &:hover {
      background: #9D0B1D;
      color: #ffffff;
    }
  }
}
.news {
  border: 0.0625em solid #A61F1F;
  border-radius: 0.625em;
  padding: 1.25em;
  &__subtitle {
    color: #a61f1f;
  }
  &-cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 2.5em;
    margin-top: 1.25em;
  }
  &-card {
    display: flex;
    &__icon {
      flex-shrink: 0;
      width: 3.75em;
      img {
        width: 100%;
      }
    }
    &__desc {
      margin-left: 1em;
      font-size: 1em;
      line-height: 120%;
      color: #8B8D8D;
      a {
        text-decoration: underline;
        color: #a61f1f;
        &:hover {
          text-decoration: none;
        }
      }
    }
  }
  &__link {
    text-align: right;
    margin-top: 2.5em;
    &-wrap {
      display: inline-flex;
      align-items: center;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 0.0625em;
        background: #a61f1f;
      }
      span {
        font-size: 1.125em;
        line-height: 120%;
        color: #a61f1f;
      }
      i {
        transform: rotate(-180deg);
        flex-shrink: 0;
        margin-left: 0.75em;
      }
      &:hover {
        &:after {
          display: none;
        }
      }
    }
  }
}


@media screen and (max-width: 992px) {
  .display2 {
    font-size: 3.125em;
  }
  .display3 {
    font-size: 2.25em;
  }
}

@media screen and (max-width: 576px) {
  .text18 {
    font-size: 1em;
  }
  .display2 {
    font-size: 2.625em;
  }
  .display3 {
    font-size: 2em;
  }
  .pagination {
    &__item {
      width: 2.25em;
      height: 2.25em;
    }
  }
  .news {
    &-card {
      &__icon {
        width: 2.5em;
      }
    }
  }
}