.articles {
  padding-bottom: 6.125em;
  &-cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 3.125em;
    margin-top: 3.125em;
  }
}
.article-card {
  display: flex;
  align-items: center;
  border-radius: 0.3125em;
  border: 0.0625em solid #C2C6C6;
  padding: 1.25em;
  &__img {
    flex-shrink: 0;
    width: 15.625em;
    img {
      width: 100%;
    }
  }
  &__desc {
    margin-left: 1.25em;
  }
  &__title {
    p {
      color: #000000;
    }
  }
  &__text {
    margin-top: 0.75em;
    p {
      color: #373737;
    }
  }
  &__flex {
    display: flex;
    justify-content: flex-end;
  }
  &__btn {
    display: inline-flex !important;
    height: 2.625em;
    margin-top: 1.25em;
    padding: 0 1.375em;
    span {
      font-size: 0.875em;
      line-height: 120%;
      font-weight: 700;
      text-transform: unset !important;
    }
  }
}

@media screen and (max-width: 1300px) {
  .articles {
    padding-bottom: 3.125em;
    &-cards {
      gap: 1.875em;
      margin-top: 2em;
    }
  }
}

@media screen and (max-width: 768px) {
  .article-card {
    align-items: start;
    &__img {
      width: 11.25em;
    }
  }
}

@media screen and (max-width: 576px) {
  .article-card {
    &__text {
      p {
        font-size: 0.875em;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .article-card {
    display: block;
    &__img {
      width: 100%;
    }
    &__desc {
      margin: 1em 0 0 0;
    }
  }
}