.main {
  background-image: url("../img/main-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 20.3125em 0 6.875em;
  color: #ffffff;
  &__title {
    font-size: 3.75em;
    line-height: 120%;
    text-transform: uppercase;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 3.75em / 2));
    gap: 2.1875em 3.75em;
    max-width: 56.875em;
    margin-top: 3.125em;
  }
  &__item {
    display: flex;
    align-items: center;
    &-icon {
      flex-shrink: 0;
      width: 5em;
      height: 5em;
      background: #ffffff;
      border-radius: 50%;
      position: relative;
      img {
        position: absolute;
        left: 0;
        bottom: 0;
        max-width: 7.875em;
      }
    }
    &-title {
      margin-left: 2.5em;
      span {
        font-size: 1.5em;
        line-height: 125%;
      }
    }
  }
  &__btn {
    width: 22.3125em;
    height: 3.8125em;
    margin-top: 3.125em;
    span {
      font-size: 1.5em;
      line-height: 120%;
    }
    i {
      flex-shrink: 0;
      margin-left: 1em;
    }
  }
}
.services {
  background: #F5F3F3;
  padding: 3.125em 0;
  overflow: hidden;
  &__swiper {
    overflow: unset;
  }
  @media screen and (min-width: 769px){
    &__swiper-wrapper {
      display: grid;
      grid-template-columns: repeat(3,calc((100% / 3) - (2em * 2 / 3)));
      gap: 2em;
      transform: unset !important;
    }
    .swiper-button-next,
    .swiper-button-prev {
      display: none;
    }
  }
}
.service-card {
  display: flex;
  flex-direction: column;
  background: #FFF;
  border-radius: 0.3125em;
  padding: 1.25em;
  position: relative;
  &:hover {
    box-shadow: 0 0.125em 0.6875em 0 rgba(0, 0, 0, 0.20);
  }
  &__text {
    max-width: 16.4375em;
    margin-top: 0.3125em;
    margin-bottom: auto;
    p {
      color: #373737;
    }
  }
  &__btn {
    width: 14.8125em;
    height: 2.625em;
    padding: 0 1em;
    margin-top: 1.5em;
    span {
      font-size: 1.125em;
      line-height: 120%;
      font-weight: 700;
    }
  }
  &__img {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 12.3125em;
    img {
      width: 100%;
    }
  }
}
.materials {
  padding: 9.375em 0 4.6875em;
  overflow: hidden;
  &2 {
    padding: 4.6875em 0;
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__link {
    height: 2.625em;
    border: 0.0625em solid #9D0B1D;
    border-radius: 0.3125em;
    padding: 0 1em;
    margin-left: 2em;
  }
  &__slider {
    margin-top: 3.125em;
  }
  &__swiper {
    overflow: unset;
  }
  @media screen and (min-width: 577px){
    &__swiper-wrapper {
      display: grid;
      grid-template-columns: repeat(4,calc((100% / 4) - (2.5em * 3 / 4)));
      gap: 2.5em;
      transform: unset !important;
    }
    .swiper-button-next,
    .swiper-button-prev {
      display: none;
    }
  }
}
.material-card {
  display: flex;
  flex-direction: column;
  background: #F5F3F3;
  border-radius: 0.625em;
  padding: 1em 2em;
  &:hover {
    box-shadow: 0 0.125em 0.6875em 0 rgba(0, 0, 0, 0.20);
  }
  &__top {
    position: relative;
  }
  &__img {
    position: relative;
    padding-bottom: 76%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__icons {
    position: absolute;
    top: -0.625em;
    right: -2em;
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625em;
  }
  &__icon {
    width: 4.6875em;
    height: 4.6875em;
    background: transparent;
    padding: 0;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__title {
    margin-top: 1.875em;
    margin-bottom: auto;
  }
  &__info {
    display: flex;
    flex-direction: column;
    margin-top: 1em;
    margin-bottom: auto;
    span:not(:first-child) {
      margin-top: 0.1875em;
    }
  }
  &__btn {
    width: 100%;
    height: 2.625em;
    margin-top: 1em;
    span {
      font-size: 1.125em;
      line-height: 120%;
    }
  }
}
.info {
  padding: 4.6875em 0;
  overflow: hidden;
  &__top {
    max-width: 73.125em;
  }
  &__title {
    span {
      color: #9D0B1D;
    }
  }
  &__text {
    margin-top: 0.75em;
    p {
      color: #373737;
    }
  }
  &__content {
    margin-top: 3.75em;
    &2 {
      margin-top: 1.25em;
    }
    &-bottom {
      margin-top: 3.75em;
    }
  }
  &__link {
    display: inline-flex;
    align-items: center;
    span {
      font-family: helioscondc;
      font-size: 2.5em;
      line-height: 120%;
      color: #000000;
      text-transform: uppercase;
      transition: all 0.5s;
    }
    i {
      flex-shrink: 0;
      overflow: hidden;
      margin-left: 0.75em;
      svg {
        margin-left: -2.75em;
        transition: all 0.5s;
      }
    }
    &:hover {
      span {
        color: #9D0B1D;
      }
      i svg {
        margin-left: 0;
      }
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 7.5em / 3));
    gap: 3.75em;
    position: relative;
    padding: 2.8125em 0;
    margin-top: 0.75em;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: 625em;
      height: 100%;
      background: #F5F3F3;
    }
  }
  &__item {
    display: flex;
    align-items: center;
    position: relative;
    z-index: 2;
    &-left {
      flex-shrink: 0;
    }
    &-right {
      margin-left: 0.5em;
    }
    &-number {
      span {
        font-family: helioscondc;
        font-size: 3.75em;
        line-height: 120%;
        font-weight: 700;
        color: #9D0B1D;
      }
    }
    &-icon {
      width: 4.375em;
      img {
        width: 100%;
      }
    }
  }
}
.catalog {
  padding: 4.6875em 0;
  &__text {
    max-width: 27.8125em;
    margin-top: 0.75em;
    p {
      color: #373737;
    }
  }
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 2.1875em;
  }
  &__img {
    width: 30.625em;
    margin-right: 7.5em;
    img {
      width: 100%;
    }
  }
  &__desc {
    width: calc(100% - 38.125em);
  }
  &__list {
    margin-top: 1.5em;
    li {
      font-size: 1em;
      line-height: 140%;
      color: #373737;
      position: relative;
      padding-left: 1.125em;
      &:after {
        content: '';
        position: absolute;
        top: 0.375em;
        left: 0;
        width: 0.625em;
        height: 0.625em;
        background: #9D0B1D;
        border-radius: 50%;
      }
      &:not(:last-child) {
        border-bottom: 0.0625em solid #F5F3F3;
        padding-bottom: 1em;
        margin-bottom: 1em;
      }
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    margin-top: 1em;
  }
  &__btn {
    height: 2.625em;
    padding: 0 2.8125em;
    span {
      font-size: 1.125em;
      line-height: 120%;
      text-transform: uppercase;
    }
    i {
      flex-shrink: 0;
      width: 1em;
      margin-left: 1em;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
  &__btn2 {
    height: 2.625em;
    padding: 0 1.875em;
    margin-left: 1.5em;
    span {
      font-size: 1.125em;
      line-height: 120%;
      font-weight: 500;
    }
    i {
      width: 1em;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
}
.materials2 {
  padding: 4.6875em 0;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__desc {
    width: 30.625em;
    margin-right: 3.125em;
  }
  &__images {
    width: calc(100% - 33.75em);
    display: flex;
    align-items: center;
    img:nth-child(1) {
      width: 30em;
      margin-right: 3.125em;
    }
    img:nth-child(2) {
      width: calc(100% - 33.125em);
    }
  }
  &__info {
    display: flex;
    flex-direction: column;
    margin-top: 1.25em;
    span:not(:first-child) {
      margin-top: 0.625em;
    }
  }
  &__btn {
    display: inline-flex;
    width: 16.5em;
    height: 2.625em;
    margin-top: 1.25em;
  }
  &__bottom {
    max-width: 38.125em;
    margin-top: 8.125em;
  }
  &__location {
    display: flex;
    margin-top: 0.875em;
    &-icon {
      flex-shrink: 0;
      width: 1em;
      svg {
        width: 100%;
        height: auto;
      }
    }
    &-text {
      margin-left: 0.625em;
      a {
        color: #9D0B1D;
      }
    }
  }
}


@media screen and (max-width: 1300px) {
  .main {
    &__title {
      font-size: 3.125em;
    }
    &__items {
      grid-template-columns: 100%;
      gap: 2.5em;
      max-width: 100%;
    }
    &__item {
      &-icon {
        width: 3.75em;
        height: 3.75em;
        img {
          max-width: 5em;
        }
      }
      &-title {
        margin-left: 1.875em;
        span {
          font-size: 1.125em;
        }
      }
    }
    &__btn {
      span {
        font-size: 1.125em;
      }
    }
  }
  .services {
    &__swiper-wrapper {
      grid-template-columns: repeat(2,calc((100% / 2) - (2em * 1 / 2)));
    }
  }
  .service-card {
    &__img {
      width: 7.5em;
    }
  }
  .materials {
    padding: 3.125em 0 1.5625em;
    &2 {
      padding: 1.5625em 0;
    }
    &__swiper-wrapper {
      grid-template-columns: repeat(2,calc((100% / 2) - (2.5em * 1 / 2)));
    }
  }
  .info {
    padding: 1.5625em 0;
    &__content {
      margin-top: 2.5em;
      &-bottom {
        margin-top: 2.5em;
      }
    }
    &__subtitle {
      span {
        font-size: 2em;
      }
    }
    &__items {
      grid-template-columns: 100%;
      gap: 2em;
    }
    &__item {
      &-number {
        span {
          font-size: 2em;
        }
      }
      &-icon {
        width: 2.5em;
      }
    }
  }
  .catalog {
    &__content {
      display: block;
    }
    &__img {
      width: 100%;
      max-width: 30.625em;
      margin: 0 0 2em 0;
    }
    &__desc {
      width: 100%;
    }
  }
  .materials2 {
    padding: 1.5625em 0 3.125em;
    &__wrap {
      display: block;
    }
    &__desc {
      width: 100%;
      margin: 0 0 2em 0;
    }
    &__images {
      width: 100%;
      justify-content: center;
      img:nth-child(1) {
        width: 18.75em;
        margin-right: 1.25em;
      }
      img:nth-child(2) {
        width: 18.75em;
      }
    }
    &__bottom {
      margin-top: 3.125em;
    }
  }
}

@media screen and (max-width: 768px) {
  .services {
    &__slider {
      position: relative;
      &-nav {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        margin-bottom: 0.625em;
      }
    }
    .swiper-button-prev {
      position: static;
      svg {
        margin-top: -0.0625em;
      }
    }
    .swiper-button-next {
      position: static;
      margin-left: 1em;
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    &__title {
      font-size: 2.625em;
    }
  }
  .service-card {
    &__btn {
      width: 12.5em;
      span {
        font-size: 1em;
      }
    }
  }
  .materials {
    &__slider {
      max-width: 25em;
      margin: 2em auto 0;
      &-nav {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        margin-top: 2em;
      }
    }
    &__swiper-wrapper {
      gap: unset;
    }
    .swiper-slide {
      opacity: 0;
      pointer-events: none;
      transition: all 0.5s;
      &-active {
        opacity: 1;
        pointer-events: all;
      }
    }
    .swiper-button-prev {
      position: static;
    }
    .swiper-button-next {
      position: static;
      margin-left: 1em;
    }
  }
  .material-card {
    &__btn {
      span {
        font-size: 1em;
      }
    }
  }
  .info {
    &__link {
      span {
        font-size: 1.75em;
      }
    }
  }
  .catalog {
    &__btns {
      display: block;
    }
    &__btn {
      width: 100%;
      span {
        font-size: 1em;
      }
    }
    &__btn2 {
      width: 100%;
      margin: 1.5em 0 0 0;
      span {
        font-size: 1em;
      }
    }
  }
  .materials2 {
    &__images {
      img:nth-child(1) {
        width: 12.5em;
      }
      img:nth-child(2) {
        width: 12.5em;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .main {
    &__btn {
      width: 100%;
    }
  }
  .service-card {
    &__img {
      width: 5em;
    }
  }
  .materials {
    &__top {
      display: block;
    }
    &__link {
      margin: 1em 0 0 0;
    }
  }
  .info {
    &__link {
      span {
        font-size: 1.5em;
      }
    }
  }
  .materials2 {
    &__images {
      img:nth-child(1) {
        width: 9.375em;
      }
      img:nth-child(2) {
        width: 9.375em;
      }
    }
  }
}