.footer {
  background: #9D0B1D;
  padding: 2.5em 0;
  color: #ffffff;
  font-family: GothamPro;
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__left {
    max-width: 16.875em;
  }
  &__center {
    display: flex;
    margin-left: 2em;
  }
  &__right {
    max-width: 20em;
    margin-left: 2em;
  }
  &__logo {
    display: block;
    width: 15.3125em;
    img {
      width: 100%;
    }
  }
  &__list {
    margin-top: 1.5625em;
    li {
      font-size: 1em;
      line-height: 135%;
      transition: all 0.5s;
      &:not(:first-child) {
        margin-top: 1em;
      }
      &:hover {
        opacity: 0.5;
      }
    }
  }
  &__nav {
    &:not(:first-child) {
      margin-left: 11.875em;
    }
    &-title {
      font-size: 1.25em;
      line-height: 135%;
      font-weight: 700;
    }
  }
  &__socials {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__social {
    width: 2.125em;
    height: 2.125em;
    background: #ffffff;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    &:not(:first-child) {
      margin-left: 0.75em;
    }
    img {
      max-width: 1.25em;
    }
  }
  &__btn {
    width: 100%;
    height: 4.125em;
    border: 0.0625em solid #FFF;
    border-radius: 0.1875em;
    margin-top: 1.5em;
    span {
      font-size: 1.125em;
      line-height: 120%;
      text-transform: uppercase;
      color: #ffffff;
    }
    &:hover {
      background: #ffffff;
      span {
        color: #000000;
      }
    }
  }
  &__brevis {
    display: flex;
    margin-top: 1.5em;
    &-icon {
      flex-shrink: 0;
      width: 2.5em;
      margin-right: 1em;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 1em;
      line-height: 120%;
      font-weight: 700;
    }
  }
}

@media screen and (max-width: 1300px) {
  .footer {
    &__right {
      margin: 2em 0 0 0;
    }
    &__nav {
      &:not(:first-child) {
        margin-left: 6.25em;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .footer {
    &__inner {
      flex-direction: column;
    }
    &__center {
      display: block;
      margin: 2em 0;
    }
    &__right {
      margin: 0;
    }
    &__nav {
      &:not(:first-child) {
        margin: 2em 0 0 0;
      }
    }
  }
}