.production {
  padding-bottom: 6.25em;
  &-cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 3.75em / 3));
    gap: 1.875em;
    margin-top: 3.125em;
  }
  &-card {
    border-radius: 0.3125em;
    border: 0.0625em solid #E9EAEB;
    background: #FFF;
    padding: 1.25em 2.1875em;
    &__img {
      position: relative;
      padding-bottom: 70%;
      img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
      }
    }
    &__title {
      margin-top: 1.875em;
      p {
        font-size: 1.25em;
        line-height: 120%;
        font-weight: 700;
        color: #101112;
      }
    }
    &__text {
      color: #9D0B1D;
      text-decoration: underline;
      text-underline-offset: 0.1875em;
      margin-top: 1.875em;
      p {
        font-size: 0.875em;
        line-height: 140%;
        font-weight: 500;
        color: #9D0B1D;
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .production {
    padding-bottom: 3.125em;
    &-cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 2.5em / 3));
      gap: 1.25em;
      margin-top: 2em;
    }
    &-card {
      padding: 1.25em;
      &__title {
        p {
          font-size: 1.125em;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .production {
    &-cards {
      grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
    }
    &-card {
      &__title {
        margin-top: 1em;
      }
      &__text {
        margin-top: 1em;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .production {
    &-card {
      &__title {
        p {
          font-size: 1em;
        }
      }
      &__text {
        p {
          font-size: 0.875em;
        }
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .production {
    &-cards {
      grid-template-columns: 100%;
    }
  }
}