.catalog-main {
  padding-bottom: 6.25em;
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.875em;
    margin-top: 3.125em;
  }
}
.catalog-card {
  display: flex;
  align-items: stretch;
  border: 0.0625em solid rgba(139, 141, 141, 0.4);
  border-radius: 0.3125em;
  &__left {
    flex-shrink: 0;
    width: 18.875em;
    margin-right: 2.5em;
    background: #E5E5E5;
    padding: 0 1em 1em;
    text-align: center;
  }
  &__right {
    padding: 2.25em 0;
  }
  &__img {
    position: relative;
    padding-bottom: 84%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__title {
    margin-top: 1.625em;
    span {
      text-transform: unset;
    }
  }
  &__flex {
    display: flex;
    .catalog-card__group {
      &:not(:first-child) {
        margin: 0 0 0 2.5em;
      }
    }
  }
  &__group {
    width: 21.875em;
    &:not(:first-child) {
      margin-top: 2em;
    }
  }
  &__subtitle {
    margin-bottom: 1.4375em;
  }
  &__list {
    li {
      font-size: 1em;
      line-height: 120%;
      color: #373737;
      &:not(:first-child) {
        margin-top: 1.25em;
      }
      &:hover {
        color: #A61F1F;
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .catalog-main {
    padding-bottom: 3.125em;
    &__cards {
      margin-top: 2em;
    }
  }
  .catalog-card {
    &__left {
      width: 15.625em;
      margin-right: 1.5em;
    }
    &__flex {
      display: block;
      .catalog-card__group {
        &:not(:first-child) {
          margin: 2em 0 0 0;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .catalog-card {
    display: block;
    &__left {
      width: 100%;
      margin: 0;
    }
    &__right {
      padding: 2em 1em;
    }
  }
}