.requisites {
  padding-bottom: 6.25em;
  &__wrap {
    background-image: url("../img/requisites-bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    margin-top: 1.875em;
    padding: 9.6875em 0;
  }
  &__desc {
    max-width: 51.125em;
  }
  &__subtitle {
    color: #ffffff !important;
  }
  &__text {
    margin-top: 0.875em;
    p {
      color: #ffffff;
    }
  }
  &__bottom {
    margin-top: 1.875em;
  }
  &__list {
    li {
      font-size: 1.0625em;
      line-height: 120%;
      color: #9D0B1D;
      position: relative;
      padding-left: 0.625em;
      &:after {
        content: '';
        position: absolute;
        top: 0.375em;
        left: 0;
        width: 0.3125em;
        height: 0.3125em;
        background: #9D0B1D;
        border-radius: 50%;
      }
      &:not(:first-child) {
        margin-top: 0.75em;
      }
    }
  }
  &__flex {
    display: flex;
    padding-right: 3.125em;
    margin-top: 1.875em;
  }
  &__left {
    width: calc(50vw - -12.5em);
    margin-right: 1.25em;
    display: flex;
    justify-content: flex-end;
    background-image: url("../img/requisites-bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 0 0.625em 0.625em 0;
    padding: 9.6875em 7.5em 9.6875em 3.125em;
    .requisites__desc {
      max-width: 52em;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }
  &__right {
    width: 33.125em;
  }
}

@media screen and (max-width: 1300px) {
  .requisites {
    padding-bottom: 3.125em;
    &__flex {
      display: block;
      padding: 0;
    }
    &__left {
      width: 100%;
      margin: 0 0 2em 0;
      border-radius: 0;
      padding: 9.6875em 0;
      .requisites__desc {
        max-width: 100%;
        padding: 0 1.25em;
      }
    }
    &__right {
      width: 100%;
      padding: 0 1.25em;
    }
  }
}

@media screen and (max-width: 768px) {
  .requisites {
    &__wrap {
      padding: 7.5em 0;
    }
    &__left {
      padding: 7.5em 0;
    }
  }
}