.gallery {
  padding-bottom: 6.25em;
  &-cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 3.75em / 3));
    gap: 1.875em;
    margin-top: 1.875em;
  }
  &-card {
    display: flex;
    flex-direction: column;
    border-radius: 0.3125em;
    border: 0.0625em solid #E9EAEB;
    background: #FFF;
    padding: 1.25em 2.1875em;
    &:hover {
      box-shadow: 0.25em 0.25em 0.5em 0 rgba(34, 60, 80, 0.2);
    }
    &__title {
      margin-bottom: auto;
      p {
        color: #101112;
        text-transform: unset !important;
      }
    }
    &__img {
      position: relative;
      padding-bottom: 70%;
      margin-top: 1.875em;
      img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
      }
    }
    &__btn {
      display: inline-flex;
      text-decoration: underline;
      text-underline-offset: 0.1875em;
      color: #9D0B1D;
      margin-top: 1.875em;
      span {
        color: #9D0B1D;
      }
      &:hover {
        text-decoration: unset;
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .gallery {
    padding-bottom: 3.125em;
    &-card {
      padding: 1.25em;
    }
  }
}

@media screen and (max-width: 768px) {
  .gallery {
    &-cards {
      grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
      gap: 1.25em;
    }
    &-card {
      &__img {
        margin-top: 1em;
      }
      &__btn {
        margin-top: 1em;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .gallery {
    &-cards {
      grid-template-columns: 100%;
    }
  }
}