.main-material {
  padding-bottom: 1.5625em;
  &__wrap {
    display: flex;
    justify-content: space-between;
    margin-top: 1.875em;
  }
  &__left {
    width: 37.5em;
    margin-right: 1em;
    position: relative;
  }
  &__right {
    width: calc(100% - 38.5em);
  }
  &__group {
    display: flex;
    flex-wrap: wrap;
    margin-top: 1.125em;
    &-left {
      width: 33.125em;
      margin-right: 1em;
    }
    &-right {
      width: calc(100% - 34.125em);
    }
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__types {
    position: absolute;
    top: 0;
    left: 0;
    display: grid;
    grid-template-columns: 100%;
    gap: 0.1875em;
  }
  .stock {
    width: 5.8125em;
    height: 1.25em;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #A61F1F;
    clip-path: polygon(0% 0%, 85% 0%, 100% 50%, 85% 100%, 0% 100%);
    padding-right: 0.3125em;
    span {
      font-size: 0.75em;
      line-height: 120%;
      color: #ffffff;
    }
  }
  .new-product {
    width: 5.8125em;
    height: 1.25em;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F1B20B;
    clip-path: polygon(0% 0%, 85% 0%, 100% 50%, 85% 100%, 0% 100%);
    padding-right: 0.3125em;
    span {
      font-size: 0.75em;
      line-height: 120%;
      color: #ffffff;
    }
  }
  &__right-top {
    display: flex;
    align-items: center;
  }
  &__rating {
    display: flex;
    align-items: center;
    span {
      color: #101112;
      margin-left: 0.625em;
      transition: all 0.5s;
    }
    &:hover {
      span {
        color: #a61f1f;
      }
    }
  }
  &__comparison {
    display: flex;
    align-items: center;
    margin-left: 2.375em;
    span {
      color: #101112;
      margin-left: 0.625em;
      transition: all 0.5s;
    }
    &:hover {
      span {
        color: #a61f1f;
      }
    }
  }
  &__right-wrap {
    border: 0.0625em solid #E9EAEB;
    border-radius: 0.1875em;
    padding: 1.875em 1.6875em;
  }
  &__stock {
    color: #101112;
  }
  &__price {
    display: flex;
    align-items: flex-end;
    margin-top: 1.125em;
    span:nth-child(1) {
      font-family: helioscondc;
      font-size: 2.75em;
      line-height: 105%;
      font-weight: 700;
      text-transform: uppercase;
      color: #101112;
    }
    span:nth-child(2) {
      font-family: helioscondc;
      font-size: 2em;
      line-height: 105%;
      font-weight: 700;
      text-transform: uppercase;
      color: #373737;
      margin-left: 0.5em;
      text-decoration: line-through;
      opacity: 0.2;
    }
  }
  &__btns {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
    margin-top: 1.125em;
  }
  &__btn {
    width: 100%;
    height: 3.75em;
    padding: 0 1em;
    span {
      font-size: 1em;
      line-height: 120%;
    }
  }
  &__btn2 {
    width: 100%;
    height: 3.75em;
    padding: 0 1em;
    span {
      font-size: 1em;
      line-height: 120%;
    }
  }
  &__nav {
    margin-top: 1.125em;
    &-title {
      color: #101112;
    }
  }
  &__list {
    margin-top: 0.5625em;
    li {
      display: flex;
      justify-content: space-between;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0.125em;
        width: 100%;
        height: 0.0625em;
        border-bottom: 0.0625em dashed #101112;
      }
      &:not(:first-child) {
        margin-top: 0.625em;
      }
      span {
        font-size: 0.875em;
        line-height: 120%;
        color: #101112;
        position: relative;
        z-index: 2;
        overflow: hidden;
        background: #ffffff;
      }
      span:nth-child(2) {
        text-align: right;
        margin-left: 1.25em;
      }
    }
  }
  &__location {
    display: flex;
    align-items: center;
    border: 0.0625em solid #E9EAEB;
    border-radius: 0.1875em;
    padding: 1em 1.25em;
    i {
      flex-shrink: 0;
      width: 1.8125em;
      margin-right: 1.5625em;
    }
    p {
      color: #101112;
    }
    p b {
      display: block;
    }
  }
  &__right-wrap2 {
    border: 0.0625em solid #E9EAEB;
    border-radius: 0.1875em;
    padding: 1.25em;
  }
  &__help {
    color: #3C3C3B;
  }
  &__text {
    margin-top: 0.75em;
    p {
      color: #8B8D8D;
    }
  }
  &__tel {
    display: block;
    color: #101112;
    margin-top: 0.75em;
    &:hover {
      color: #a61f1f;
    }
  }
  &__mail {
    display: inline-flex !important;
    height: 2.625em;
    padding: 0 1em;
    margin-top: 0.75em;
  }
}
.info-material {
  padding: 1.5625em 0;
  &__tabs {
    &-head {
      display: flex;
      align-items: center;
      border-bottom: 0.0625em solid #E5E5E5;
    }
    &-body {
      margin-top: 3.125em;
    }
  }
  &__tab {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 2.5em;
    border-radius: 0.1875em;
    border: 0.0625em solid #E5E5E5;
    padding: 0 2em;
    cursor: pointer;
    transition: all 0.5s;
    span {
      font-size: 0.875em;
      line-height: 120%;
      color: #101112;
    }
    &--active {
      background: #9D0B1D;
      border-radius: 0.3125em;
      text-align: center;
      span {
        color: #ffffff;
      }
    }
    &:hover {
      background: #9D0B1D;
      span {
        color: #ffffff;
      }
    }
  }
  &__content {
    display: none;
    &--active {
      display: block;
    }
  }
  &__group {
    &:not(:first-child) {
      margin-top: 1.875em;
    }
  }
  &__subtitle {
    margin-bottom: 1.875em;
  }
  &__text {
    &:not(:first-child) {
      margin-top: 1.875em;
    }
  }
  &__list {
    li {
      font-size: 1em;
      line-height: 120%;
      color: #000000;
      position: relative;
      padding-left: 1.25em;
      &:not(:first-child) {
        margin-top: 0.3125em;
      }
      &:after {
        content: '';
        position: absolute;
        top: 0.3125em;
        left: 0;
        width: 0.625em;
        height: 0.625em;
        border-radius: 50%;
        background: #9D0B1D;
      }
    }
  }
  &__btn {
    display: inline-flex !important;
    height: 3.75em;
    padding: 0 3.125em;
    margin-top: 1.875em;
    i {
      width: 1.5em;
      margin: 0 0.625em 0 0 !important;
      svg {
        width: 100%;
        height: auto;
      }
    }
    span {
      font-size: 1em;
      line-height: 120%;
    }
  }
}
.calculator-material {
  padding: 1.5625em 0 3.125em;
  color: #ffffff;
  &__inner {
    display: flex;
    justify-content: space-between;
    max-width: 78.125em;
    margin: 0 auto;
    background: #9D0B1D;
    padding: 0 3.125em;
  }
  &__desc {
    max-width: 46.875em;
    padding: 2em 0;
  }
  &__img {
    width: 15.625em;
    margin-left: 3em;
    img {
      width: 100%;
    }
  }
  &__title {
    font-size: 2.125em;
    line-height: 120%;
  }
  &__text {
    display: flex;
    padding-left: 1.25em;
    margin-top: 1.5em;
  }
  &__btn {
    display: inline-flex !important;
    height: 2.625em;
    border: 0.0625em solid #ffffff;
    border-radius: 0.3125em;
    padding: 0 1em;
    margin-left: 2em;
    span {
      font-size: 1em;
      line-height: 120%;
      text-transform: uppercase;
      margin-left: 1.5em;
    }
    i {
      flex-shrink: 0;
      margin-left: 0.5em;
      transform: rotate(-180deg);
      svg path {
        fill: #ffffff;
      }
    }
  }
}


@media screen and (max-width: 1300px) {
  .main-material {
    &__wrap {
      flex-wrap: wrap;
    }
    &__left {
      width: 21.875em;
    }
    &__right {
      width: 28.125em;
    }
    &__group {
      display: block;
      &-left {
        width: 100%;
        margin: 0 0 2em 0;
      }
      &-right {
        width: 100%;
      }
    }
    &__right-top {
      display: block;
    }
    &__comparison {
      margin: 1em 0 0 0;
    }
    &__price {
      flex-direction: column;
      justify-content: start;
      align-items: start;
      span:nth-child(1) {
        font-size: 2.25em;
      }
      span:nth-child(2) {
        font-size: 1.875em;
        margin: 0.5em 0 0 0;
      }
    }
  }
  .calculator-material {
    &__inner {
      max-width: unset;
      width: calc(100% + 2.5em);
      margin-left: -1.25em;
      padding: 1.25em 1em;
    }
    &__desc {
      max-width: 100%;
      padding: 0;
    }
    &__img {
      display: none;
    }
    &__text {
      display: block;
      padding-left: 0;
    }
    &__btn {
      margin: 1em 0 0 0;
    }
  }
}

@media screen and (max-width: 768px) {
  .main-material {
    &__wrap {
      display: block;
    }
    &__left {
      width: 100%;
      margin: 0 0 2em 0;
    }
    &__right {
      width: 100%;
    }
    &__btns {
      display: block;
    }
    &__btn {
      display: inline-flex !important;
      width: auto;
      height: 2.625em;
      &:not(:first-child) {
        margin-top: 1em;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .main-material {
    &__btn {
      span {
        font-size: 0.875em;
      }
    }
    &__price {
      span:nth-child(1) {
        font-size: 2em;
      }
      span:nth-child(2) {
        font-size: 1.5em;
      }
    }
  }
  .info-material {
    &__tab {
      padding: 0 1em;
    }
    &__btn {
      height: 2.625em;
    }
  }
}

@media screen and (max-width: 400px) {
  .info-material {
    &__tabs {
      &-head {
        display: block;
        padding-bottom: 1.5625em;
      }
      &-body {
        margin-top: 1.5625em;
      }
    }
    &__tab {
      &:not(:first-child) {
        margin-top: 1em;
      }
    }
  }
}