.main-news {
  padding-bottom: 6.25em;
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.875em;
    margin-top: 1.875em;
  }
  &__card {
    display: flex;
    border: 0.0625em solid #C2C6C6;
    border-radius: 0.3125em;
    padding: 1.25em;
    &-icon {
      flex-shrink: 0;
      width: 5.125em;
      img {
        width: 100%;
      }
    }
    &-desc {
      margin-left: 1.25em;
    }
    &-title {
      span {
        color: #000000;
      }
    }
    &-text {
      margin-top: 0.75em;
      p {
        color: #373737;
      }
    }
    &-date {
      margin-top: 0.75em;
      span {
        color: #373737;
        opacity: 0.4;
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .main-news {
    padding-bottom: 3.125em;
    &__card {
      &-icon {
        width: 3.125em;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .main-news {
    &__card {
      padding: 1.25em 1em;
      &-desc {
        margin-left: 1em;
      }
    }
  }
}