.main-about {
  background-image: url("../img/main-about__bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 13.75em 0;
  &__title {
    color: #ffffff !important;
  }
}
.main2-about {
  .container {
    max-width: 100%;
  }
  &__inner {
    display: flex;
    align-items: stretch;
  }
  &__left {
    width: calc(50vw - -20em);
    display: flex;
    justify-content: flex-end;
    &-wrap {
      max-width: 71.875em;
      height: 100%;
      background-image: url("../img/main-about__bg.webp");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      padding: 10.9375em 1.125em 10.9375em 13.4375em;
    }
  }
  &__right {
    width: 35.625em;
    margin-left: 1.25em;
  }
  &__title {
    color: #ffffff !important;
  }
}
.info-about {
  padding: 3.125em 0;
  background: #F5F3F3;
  &__top {
    max-width: 42.8125em;
    margin: 0 auto;
    text-align: center;
  }
  &__img {
    width: 24.1875em;
    margin: 3.125em auto 0;
    img {
      width: 100%;
    }
  }
  &__text {
    margin-top: 3.125em;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 3.75em / 3));
    gap: 1.875em;
    margin-top: 3.125em;
  }
  &__card {
    background: #FFF;
    border-radius: 0.1875em;
    text-align: center;
    padding: 3.125em 2.5em;
    &-icon {
      width: 4.3125em;
      height: 4.3125em;
      border: 0.3125em solid #C2C6C6;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 auto;
      img {
        max-width: 2.125em;
        width: 100%;
      }
    }
    &-text {
      margin-top: 1.5em;
    }
  }
}
.info2-about {
  padding: 3.125em 0;
  &__title {
    text-align: center;
  }
  &__cards {
    width: calc(100% + 3.125em);
    display: flex;
    flex-wrap: wrap;
    margin-top: 3.125em;
  }
  &__card {
    flex: 1 0 calc(22% - 3.125em);
    border: 0.0625em solid #C2C6C6;
    border-radius: 0.1875em;
    padding: 1.25em;
    margin-right: 3.125em;
    margin-bottom: 3.125em;
    &-icon {
      width: 5.125em;
      img {
        width: 100%;
      }
    }
    &-title {
      margin-top: 0.75em;
      p {
        font-size: 1.5em;
        line-height: 120%;
        font-weight: 700;
        color: #000000;
      }
    }
    &-list {
      margin-top: 0.75em;
      li {
        font-size: 1em;
        line-height: 120%;
        color: #373737;
        &:not(:first-child) {
          margin-top: 0.625em;
        }
      }
    }
    &-text {
      margin-top: 0.75em;
      p {
        font-size: 1em;
        line-height: 120%;
        color: #373737;
        &:not(:first-child) {
          margin-top: 0.625em;
        }
      }
    }
  }
}


@media screen and (max-width: 1300px) {
  .main2-about {
    &__inner {
      display: block;
    }
    &__left {
      width: calc(100% + 2.5em);
      margin-left: -1.25em;
      background-image: url("../img/main-about__bg.webp");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      padding: 7.5em 1.25em;
      &-wrap {
        max-width: 100%;
        padding: 0;
      }
    }
    &__right {
      width: 100%;
      margin: 1.25em 0 0 0;
    }
  }
  .info-about {
    &__img {
      margin-top: 2em;
    }
    &__text {
      margin-top: 2em;
    }
    &__cards {
      margin-top: 2em;
    }
    &__card {
      padding: 2em 1em;
    }
  }
  .info2-about {
    &__cards {
      width: 100%;
      display: grid;
      grid-template-columns: repeat(3, calc(100% / 3 - 3.75em / 3));
      gap: 1.875em;
    }
    &__card {
      margin: 0;
      &-icon {
        width: 3.125em;
      }
      &-title {
        p {
          font-size: 1.125em;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .main-about {
    padding: 7.5em 0;
  }
  .info-about {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
      gap: 1.25em;
    }
  }
  .info2-about {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
      gap: 1.25em;
    }
  }
}

@media screen and (max-width: 576px) {
  .info-about {
    &__card {
      &-icon {
        width: 3.125em;
        height: 3.125em;
        img {
          max-width: 1.5em;
        }
      }
      &-text {
        margin-top: 1em;
        p {
          font-size: 0.875em;
        }
      }
    }
  }
  .info2-about {
    &__card {
      &-title {
        p {
          font-size: 1em;
        }
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .info-about {
    &__img {
      width: 100%;
    }
    &__cards {
      grid-template-columns: 100%;
    }
  }
  .info2-about {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}