.contacts {
  background: #F5F3F3;
  padding: 3.125em 0;
  margin-top: 0.75em;
  .map {
    width: 100%;
    height: 22.8125em;
  }
  &__info {
    background: #FFF;
    padding: 2.5em 1.875em;
    margin-top: 3.125em;
    &-wrap {
      max-width: 50.375em;
    }
    &-title {
      font-family: helioscondc;
      font-size: 2.375em;
      line-height: 120%;
      font-weight: 700;
      color: #101112;
    }
  }
  &__location {
    display: block;
    margin-top: 0.875em;
    p {
      font-size: 1.25em;
      line-height: 140%;
      color: #9D0B1D;
    }
  }
  &__tel {
    display: block;
    margin-top: 2.5em;
    span {
      font-size: 0.875em;
      line-height: 120%;
      font-weight: 700;
      color: #9d0b1d;
      transition: all 0.5s;
    }
    &:hover {
      span {
        color: #8d0517;
      }
    }
  }
  &__mail {
    display: inline-flex;
    align-items: center;
    margin-top: 0.75em;
    &-desc {
      display: flex;
      align-items: center;
      span {
        font-size: 1em;
        line-height: 120%;
        color: #8B8D8D;
        transition: all 0.5s;
      }
      i {
        flex-shrink: 0;
        display: flex;
        align-items: center;
        margin-left: 0.25em;
        transition: all 0.5s;
      }
    }
    &-icon {
      flex-shrink: 0;
      width: 1.875em;
      height: 1.875em;
      background: #9D0B1D;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: 1em;
      svg {
        width: 1em;
      }
    }
    &:hover {
      .contacts__mail-desc {
        span {
          color: #9D0B1D;
        }
        i svg path {
          fill: #9D0B1D;
        }
      }
    }
  }
  &__info-subtitle {
    margin-top: 0.75em;
    span {
      font-size: 0.875em;
      line-height: 120%;
      font-weight: 700;
      color: #101112;
    }
  }
  &__chart {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.375em;
    margin-top: 0.75em;
    span {
      font-size: 0.875em;
      line-height: 120%;
      color: #101112;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(100% / 2 - 1.875em / 2));
    gap: 1.875em;
    margin-top: 0.75em;
  }
  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 5em;
    border-radius: 0.3125em;
    border: 0.0625em solid #E9EAEB;
    background: #FFF;
    padding: 0 1em;
    i {
      flex-shrink: 0;
      width: 2.5em;
      margin-right: 1.875em;
      svg {
        width: 100%;
        height: auto;
      }
    }
    span {
      font-size: 0.875em;
      line-height: 120%;
      font-weight: 700;
      color: #9D0B1D;
      text-decoration: underline;
      text-underline-offset: 0.1875em;
    }
    &:hover {
      background: #9D0B1D;
      i svg path {
        fill: #ffffff;
      }
      span {
        color: #ffffff;
        text-decoration: none;
      }
    }
  }
  &__text {
    margin-top: 2.5em;
    p {
      font-size: 1em;
      line-height: 140%;
      color: #101112;
      a {
        color: #9D0B1D;
      }
    }
  }
  &__news {
    background: #FFF;
    border-radius: 0.3125em;
    padding: 3.125em 0;
    text-align: center;
    margin-top: 6.25em;
  }
  &__form {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1.25em;
    &-group {
      height: 3.75em;
      padding: 0;
      margin: 0;
      border: none;
      &:not(:first-child) {
        margin-left: 1.5em;
      }
      input {
        width: 24.0625em;
        height: 100%;
        background: #FFF;
        border: 0.0625em solid #E9EAEB;
        border-radius: 0.1875em;
        padding: 0 0.75em;
        font-size: 1em;
        line-height: 120%;
        color: #000000;
        &::placeholder {
          color: #757575;
        }
      }
    }
    &-btn {
      height: 100%;
      padding: 0 2.5em;
      background: transparent;
      span {
        font-size: 1em;
        line-height: 120%;
      }
    }
  }
  &__feedback {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 3.75em / 3));
    gap: 1.875em;
    margin-top: 9.0625em;
    &-item {
      display: flex;
      justify-content: center;
      align-items: center;
      min-height: 10.625em;
      background: #FFF;
      border: 0.0625em solid #E9EAEB;
      border-radius: 0.3125em;
      padding: 1.25em;
    }
    &-icon {
      flex-shrink: 0;
      width: 7.6875em;
      img {
        width: 100%;
      }
    }
    &-title {
      color: #006843;
      margin-left: 2.8125em;
      span {
        font-size: 1em;
        line-height: 120%;
        color: #006843;
        text-decoration: underline;
        text-underline-offset: 0.1875em;
        &:hover {
          text-decoration: none;
        }
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .contacts {
    &__info {
      margin-top: 2em;
    }
    &__text {
      margin-top: 1.25em;
    }
    &__news {
      margin-top: 3.125em;
    }
    &__feedback {
      grid-template-columns: repeat(3, calc(100% / 3 - 2.5em / 3));
      gap: 1.25em;
      margin-top: 3.125em;
      &-item {
        padding: 1em;
      }
      &-icon {
        width: 5em;
      }
      &-title {
        margin-left: 1em;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .contacts {
    &__info {
      width: calc(100% + 2.5em);
      margin-left: -1.25em;
      padding: 1.875em 1.25em;
    }
    &__items {
      grid-template-columns: 100%;
      gap: 1em;
    }
    &__news {
      width: calc(100% + 2.5em);
      margin-left: -1.25em;
      padding: 1.875em 1.25em;
    }
    &__form {
      display: block;
      &-group {
        width: 18.75em;
        height: 3.125em;
        margin-left: auto !important;
        margin-right: auto !important;
        &:not(:first-child) {
          margin: 1em 0 0 0;
        }
        input {
          width: 100%;
        }
      }
      &-btn {
        width: 100%;
        margin: 0 auto;
      }
    }
    &__feedback {
      grid-template-columns: 100%;
    }
  }
}